import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/view/index'
import Reserve from '@/view/reserve'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/reserve',
      name: 'reserve',
      component: Reserve
    }
  ]
})

export default router



